footer {
  margin: 10vh 1vw;
}

footer h1 {
  font-size: 160%;
  font-weight: 500;
}

footer p {
  font-size: 120%;
}


/* Formatting Classes */

.lg-img {
  width: 60vw;
  height: auto;
  padding-left: 10vw;
}

.med-img {
  width: 100%;
  height: auto;
}

.small-img {
  width: 50%;
  height: auto;
}

.xs-img {
  width: 200px;
  height: auto;
}


h1.intro{
  /* font-size: 220%; */
  font-size: 2.25rem;
  font-weight: 400;
  padding: 0px 10px;
}

h2.intro{
  /* font-size: 160%; */
  font-size: 1.5rem;
  font-weight: 400;
  padding: 10px;
}

h1.card{
  font-size: 180%;
  font-weight: 600;
  /* padding-left: 1rem; */
  color: black;
}

h1.card:hover{
  color: #bdc8db;
}

h2.card{
  font-size: 140%;
  font-weight: 400;
  margin-top: 4px;
}

.project h1 {
  padding-top: 80px;
}

.project h2 {
  color: #8E9AAF;
  font-weight: 600;
}

h1.small-card{
  font-size: 150%;
  font-weight: 500;
  padding: 0;
  margin: 0;
}

.project strong {
  font-weight: 800;
}

.large-text {
  font-size: large;
}

.horizontal-list ul {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.horizontal-list li {
  display: block;
  list-style-type: none;
  flex: 0 1 auto;
}

.horizontal-list p {
  line-height: 0.5;
  /* margin-bottom: 4%; */
}

.vertical-list ul {
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.vertical-list li {
  display: block;
  list-style-type: none;
  flex: 0 1 auto;
  padding-bottom: 10px;
}

.vertical-list p {
  line-height: 0.5;
}

.border {
  border: 2px solid #C7C7C7;
}

.light-text {
  color: #808080;
  padding: 0;
  margin-bottom: 4px;
}

.no-top-margin {
  margin-top: 0;
}

.no-bot-margin {
  margin-bottom: 0;
}




/* Formatting IDs */

#logo {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-size: 28px;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: black;
}

#nav-links {
  text-align: right;
  float: right;
  padding: 10px 0px;
}

#nav-links ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  line-height: 2;
}

#nav-links li {
  display: inline;
}

#nav-links li a, #nav-links-mobile a {
  font-size: 80%;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: #2F323B;
  padding: 12px;
}

#nav-links li a:hover, #nav-links-mobile a:hover {
  font-weight: 600;
  color: #bdc8db;
}

#social-icons a {
  color: #2F323B;
  margin-right: 24px;
}

#social-icons a:hover {
  color: #bdc8db;
}



#numbered-list h1 {
  padding: 0;
  margin: 0;
}

#takeaways h1 {
  margin: 0;
  padding-top: 50px;
}

#other-project h1 {
  font-size: 180%;
}

#info-icon {
  padding: 4% 26%
}

#updates li {
  line-height: 1.4;
}

#comp-logo {
  width: 170px;
  height: auto;
}


/* Media queries */
@media screen and (max-width: 899px) {
  h1.card {
    margin-top: 8px;
  }
  
  h2.card{
    margin-top: 0px;
  }

  footer {
    padding: 8vw;
  }
}

@media screen and (max-width: 480px) {
  .small-img {
    width: 100%;
    max-width: 100%;
  }

  h1.intro{
    font-size: 2rem;
  }
  
  h2.intro{
    font-size: 1.25rem;
  }

  h1 {
    font-size: 220%;
    font-weight: 400;
  }
  
  h2 {
    font-size: 160%;
    font-weight: 400;
  }

  h1.card{
    font-size: 1.75rem;
  }
  
  h2.card{
    font-size: 1.25rem;
  }

  #aboutme img, #aboutme p {
      padding: 15px 0px;
  }



}

